.App {
  /* text-align: center; */
}

/* rename and fix left for mobile */

@-webkit-keyframes duck-slider {
  0% {
    left: -15%;
  }
  100% {
    left: 100%;
  }
}

@keyframes duck-slider {
  0% {
    left: -15%;
  }
  100% {
    left: 100%;
  }
}

.App-logo {
  -webkit-animation: duck-slider infinite 6s linear;
          animation: duck-slider infinite 6s linear;
  height: 10em;
  position: absolute;
  left: -15%;
}

.App-logo-mobile {
  height: 10em;
}

.App-header {
  background-color: #ffffff;
  padding: 1em;
  color: black;
  display: block;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 150px;
}

.App-title {
  padding: 0 1em;
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.App-content-wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}



